import Vue from "vue";

import Vuex from 'vuex'

Vue.use(Vuex)

const actions = {
    change(context, value) {
        context.commit('CHANGE', value)
    }
}

const mutations = {
    CHANGE(state, value) {
        state.scroH = value;
    }
}

const state = {
    num: 0,
    scroH: 0,
}

const getters = {
    theScroH() {
        return state.scroH / 20 - 350;
    }
}

export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters,
})